@font-face {
  font-family: Roboto-Regular;
  src: url("../fonts/Roboto/Roboto-Regular.ttf");
}
@font-face {
  font-family: Roboto-Medium;
  src: url("../fonts/Roboto/Roboto-Medium.ttf");
}
@font-face {
  font-family: Roboto-Bold;
  src: url("../fonts/Roboto/Roboto-Bold.ttf");
}
/*--> Common styles START <--*/
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 10pt;
}
a {
  outline: none;
  text-decoration: none;
}
.container {
  display: flex;
  width: 1050px;
}
/*--> Common styles END <--*/
/*--> Header START <--*/
.header {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  height: 100vh;
  background-color: #f00;
}
.header-top-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #f2f2f2;
  height: 46px;
}
.header-top-bar-logo {
  display: flex;
}
.header-top-bar-logo__img {
  display: flex;
  height: 46px;
}
.header-top-bar-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-grow: 1;
}
.header-top-bar-nav__list {
  display: flex;
  list-style-type: none;
  flex-direction: row;
  justify-content: space-between;
  width: 450px;
  text-transform: uppercase;
}
.header-top-bar-nav__list--item a {
  color: #313131;
}
.header-top-bar-nav__list--item_selected a {
  color: #313131;
  font-weight: bold;
}
.header-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  flex-grow: 1;
  background: url("../img/people-coffee-tea-meeting.jpg");
  background-size: cover;
}
.header-slider-container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.header-slider-container-title {
  display: flex;
  width: auto;
  flex-grow: 1;
  text-transform: uppercase;
  font-size: 3rem;
  color: #f2f2f2;
}
.header-slider-container-description {
  display: flex;
  flex-grow: 1;
  text-transform: uppercase;
  margin-bottom: 50px;
  color: #f2f2f2;
}
.header-slider-container-form {
  display: flex;
  flex-grow: 1;
}
.header-slider-container-form__join-btn {
  display: flex;
}
.header-slider-container-form__works-btn {
  display: flex;
}
/*--> Header END <--*/
/*--> AboutUs START <--*/
.about-us {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  height: 100vh;
  background-color: #11b8cc;
}
.about-us .title-about-us {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100px;
  background-color: #424242;
}
.about-us .title-about-us h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: #dfdfdf;
}
.about-us .about-us__who-we-are {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  background-color: #f00;
}
.about-us .about-us__what-we-do {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  background-color: #008000;
}
.about-us .about-us__how-we-do-it {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  background-color: #ff0;
}
/*--> AboutUs END <--*/
